:root {
    --app-background-dark: #181720;
    --app-star: #ebeb00;
}

body {
    background-color: var(--app-background-dark);
}

.container {
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
}

main {  
    max-height: 100svh;
}

h1, h2, h3, p, li, a, label, button {
    color: #fff;
}

/* Star styling */

.star-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100svh;
}

.column {
    display: flex;
    gap: 15px;
}

.star-icons {
    cursor: pointer;
    width: 15rem;
    height: 20rem;
    color: #3b384d;
    transition: all 0.2s ease-in-out;
}

.text-rating {
    width: 20rem;
    height: 10rem;
    font-size: 4rem;
    font-weight: 600;
    text-align: center;
}

.star-icons.selected {
    color: var(--app-star);
}
/* Dialog styling */

.dialog-btn {
    font-size: 1.9rem;
    font-weight: 600;
    position: fixed;
    bottom: 20px;
    right: 40px;
    width: 15rem;
    height: 5rem;
    border-radius: 5px;
    background-color: #225EEC;
    color: #fff;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
}

.dialog-btn:hover {
    background-color: #1146c2;
}

.center-dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40rem;
    height: 30rem;
    background-color: #6e6992;
}

.dialog-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px 30px;
}

h2 {
    font-size: 3rem;
    text-align: center;
}

p {
    font-size: 1.7rem;
    margin-top: 5px;
}

.dialog-content input[type=button] {
    display: flex;
    width: 12rem;
    height: 4rem;
    align-items: center;
    border-radius: 5px;
    font-size: 1.7rem;
    font-weight: 600;
    margin-top: 15px;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
}

.dialog-content input[type=button]:hover {
    background-color: #656187;
}

.hide-stars {
    display: none;
}